    "Heading": "Got Plans for Spring Break?",
    "SubHeading": "5 ways to stay busy when Hillsborough County's public schools take a breather in mid-March",
    "Body": "<p>Looking for something to do during spring break?<\/p>\r\n<p>The annual leave of absence is March 18-22 for students at Hillsborough County public schools.<\/p>\r\n<p>The County offers plenty of fun, wholesome activities for students of all ages. <\/p>\r\n<p>Here are a few:<\/p>\r\n<p>At <a href=\"\/en\/residents\/recreation-and-culture\/kids\/camp-days\">Spring Break Camp,<\/a> children in kindergarten through eighth grade spend time with their friends, and meet new ones, in a supervised, fun-filled atmosphere. And it's not too soon to consider registering children and teens for <a href=\"https:\/\/www.hillsboroughcounty.org\/en\/residents\/recreation-and-culture\/kids\/action-folder\/register-for-summer-camp\">Summer Camp.<\/a> Programs are available for kids with varied interests and abilities, and <a href=\"\/en\/newsroom\/2019\/03\/07\/you-may-qualify-for-summer-camp-discounts\">you might qualify for financial assistance<\/a>.<\/p>\r\n<p>Off-the-beaten path <a href=\"https:\/\/www.hillsboroughcounty.org\/en\/newsroom\/2018\/06\/20\/beat-the-heat-at-these-distinctive-museums\">museums and attractions<\/a> are a fun and interesting way to spend a day, or more. <a href=\"https:\/\/www.hillsboroughcounty.org\/en\/newsroom\/2018\/06\/08\/discovery-pass\">Discovery Pass,<\/a> available to anyone with a Hillsborough County Public Library Cooperative card, provides discounts to some of these, and other, museums and attractions.<\/p>\r\n<p>Get outdoors. Plan a <a href=\"https:\/\/www.hillsboroughcounty.org\/en\/newsroom\/2017\/05\/26\/roll-out-to-a-hillsborough-county-bicycle-trail\">bicycle ride, <\/a> an excursion to a <a href=\"https:\/\/www.hillsboroughcounty.org\/en\/newsroom\/2017\/03\/03\/u-pick\">U-Pick field,<\/a> an <a href=\"https:\/\/www.hillsboroughcounty.org\/en\/newsroom\/2017\/08\/23\/hillsborough-has-gone-to-the-dogs\">outing with your dog,<\/a> or a <a href=\"https:\/\/www.hillsboroughcounty.org\/en\/residents\/recreation-and-culture\/outdoor-recreation\/camping\">camping trip.<\/a> The annual <a href=\"https:\/\/www.hillsboroughcounty.org\/en\/calendar\/03-parksandrec\/2019\/20190316-great-american-campout\">Great American Campout,<\/a> for kids and families, is March 16 and 17, the first weekend of spring break.<\/p>\r\n<p>Line up a <a href=\"https:\/\/www.hillsboroughcounty.org\/en\/newsroom\/2019\/02\/12\/summer-job-opportunities-with-hillsborough-county\">summer job.<\/a> Hillsborough County is hiring teens for part-time and seasonal jobs including lifeguard, recreation leader, and intern in various departments.<\/p>\r\n<p><a href=\"https:\/\/www.hillsboroughcounty.org\/en\/residents\/citizens\/volunteers\">Volunteer<\/a> during spring break, or throughout the year. Teens are especially helpful with Children's Services and Head Start programs.<\/p>",
    "Excerpt": "Looking for something to do during spring break? The annual leave of absence is March 18-22 for...",
